import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import TranslationSection from "./TranslationSection/TranslationSection";
import TitleSection from "./TitleSection/TitleSection";
import LegalizationSection from "./Legalization/LegalizationSection";
import ProofreadingSection from "./ProofreadingSection/ProofreadingSection";
import InterpretationSection from "./InterpretationSection/InterpretationSection";


export default class ServicePage extends PureComponent {
  render() {
    const {preTitle} = this.props.titleSection;

    return (
        <div>
          <TitleSection title={this.props.titleSection.title}/>
          <TranslationSection {...this.props.translation} preTitle={preTitle}/>
          <InterpretationSection {...this.props.interpretation} preTitle={preTitle}/>
          <ProofreadingSection {...this.props.proofreading}/>
          <LegalizationSection {...this.props.legalization} preTitle={preTitle}/>
        </div>
    );
  }
}

ServicePage.propTypes = {
  translation: PropTypes.object.isRequired,
  legalization: PropTypes.object.isRequired,
  titleSection: PropTypes.object.isRequired,
  proofreading: PropTypes.object.isRequired,
  interpretation: PropTypes.object.isRequired,
};


