import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import './style.css'

export default class InterpretationSection extends PureComponent {
  render() {
    return (
        <section className="container interpretation-section">
          <div className="row">

            <div className="col-lg-6 col-md-12 sector-description">
              <div className="section-title">
                <p className="top">{this.props.preTitle}</p>
                <h2 className="title">{this.props.title}</h2>
              </div>

              <p>{this.props.text1}</p>
              <p>{this.props.text2}</p>
              <p>{this.props.text3}</p>
            </div>

            <div className="col-lg-5 offset-lg-1 col-md-12">
              <div className="row">
                <div className="col-12 col-md-6 col-lg-12 icon-box">
                  <div className='icon-box-title'>
                    <i className="fa fa-superpowers"/>
                    <h5>{this.props.consecutiveTitle}</h5>
                  </div>
                  <p>{this.props.consecutiveText}</p>
                </div>

                <div className="col-12 col-md-6 col-lg-12 icon-box">
                  <div className='icon-box-title'>
                    <i className="fa fa-comments-o"/>
                    <h5>{this.props.chuchotageTitle}</h5>
                  </div>
                  <p>{this.props.chuchotageText}</p>
                </div>
              </div>
            </div>
          </div>
        </section>
    );
  }
}

InterpretationSection.propTypes = {
  preTitle: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  text1: PropTypes.string.isRequired,
  text2: PropTypes.string.isRequired,
  text3: PropTypes.string.isRequired,
  consecutiveTitle: PropTypes.string.isRequired,
  consecutiveText: PropTypes.string.isRequired,
  chuchotageTitle: PropTypes.string.isRequired,
  chuchotageText: PropTypes.string.isRequired,
};



