import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';

import './style.css';
import ContactForm from './ContactForm';

export default class ContactSection extends PureComponent {
  render() {
    const {phone, phonePortugal} = this.props.contactInfo;
    const phoneHref = phone ? 'tel:' + phone.replace(/\(|\)| /g, '') : '';
    const phonePortugalHref = phonePortugal ? 'tel:' + phonePortugal.replace(/\(|\)| /g, '') : '';

    return (
        <section className="greyWrapper contact-section">
          <div className="container">
            <div className="row">

              {/*about text*/}
              <div className="col-md-6 col-sm-12 order-md-1 order-2">
                <ContactForm {...this.props.contactForm}/>
              </div>

              {/*contact info*/}
              <div className="col-md-5 offset-md-1 col-sm-12 order-md-2 order-1 company-info-wrapper">
                <h2 className="text-right">Contact</h2>
                <div className="company-info mb-5">
                  <ul>
                    <li><a href={phoneHref}><i className="fa fa-phone fa-fw"/>{phone}</a></li>
                    <li><a href={phonePortugalHref}><i className="fa fa-phone fa-fw"/>{phonePortugal}</a></li>
                    <li><i className="fa fa-envelope fa-fw"/>{this.props.contactInfo.email}</li>
                    <li><i className="fa fa-map-marker fa-fw"/>{this.props.contactInfo.location}</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </section>
    );
  }
}

ContactSection.propTypes = {
  contactForm: PropTypes.object.isRequired,
  contactInfo: PropTypes.shape({
    phone: PropTypes.string.isRequired,
    phonePortugal: PropTypes.string.isRequired,
    email: PropTypes.string.isRequired,
    location: PropTypes.string.isRequired
  }).isRequired
};




